.animatable-default {
    position: relative;
    clip-path: polygon(0 0, 0 0, 0 100%, 0% 100%);
    animation: .75s cubic-bezier(1, -0.01, .12, .8) 0.25s 1 reveal forwards;
    display: inline-block;
}

#skills .animatable-default {
    padding-bottom: 9px;
}

.animatable-default::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(200deg, #FC736C, #f1a836);
    padding: inherit;
    animation: .50s cubic-bezier(1, -0.01, .12, .8) 1s 1 hideBlack forwards;
}

.animatable-default::after {
    content: '';
    position: absolute;
    top: 0;
