.animatable-pic {
    position: absolute;
    bottom: 0;
    right: 12%;
    clip-path: polygon(0 0, 0 0, 0 100%, 0% 100%);
    animation: .75s cubic-bezier(1,-0.01,.12,.8) 2s 1 reveal forwards;
    padding-left: 10px;
}

.animatable-pic::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(200deg, #FC736C, #f1a836);
    padding: inherit;
    animation: .50s cubic-bezier(1,-0.01,.12,.8) 3s 1 hideBlack forwards;
}

.animatable-pic img {
    height: 62vh;
    filter: brightness(.9);
}

@media (max-width: 800px) {
    .animatable-pic, .animatable-pic img {
        visibility: hidden;
    }
}





@keyframes reveal {
    from {
        clip-path: polygon(0 0, 0 0, 0 100%, 0% 100%);
    }

    to {
        clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
    }
}

@keyframes hideBlack {
    from {
        clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
    }

    to {
        clip-path: polygon(100% 0, 100% 0, 100% 100%, 100% 100%);
    }
}