@for $i from 1 through 5 {
  #inner-circle#{$i} {
    visibility: hidden;
    position: absolute;
    top: 133px;
    left: 215px;
    border-radius: 50%;
    width: 250px;
    height: 250px;
    background: #000;
  }
}


$color-background: #F5F5F5;
$color-main: #000;
$color-active: #56c5d1;
$color-link: #FFF;
$color-btn1: #f1a836;

$button-height: 27px;
$button-width: 35px;


.button_container {
  position: fixed;
  top:3%;
  right: 3%;
  height: $button-height;
  width: $button-width;
  cursor: pointer;
  z-index: 100;
  transition: opacity .25s ease;
  
  &:hover {
    opacity: .7;
  }
  
  &.active {
    .top {
      transform: translateY(11px) translateX(0) rotate(45deg);
      background: $color-active;
    }
    .middle {
      opacity: 0;
      background: $color-active;
    }
    
    .bottom {
      transform: translateY(-11px) translateX(0) rotate(-45deg);
      background: $color-active;
    }
  }
  
  span {
  background: $color-btn1;
  border: none;
  height: 3px;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transition:  all .65s ease;
  cursor: pointer;
    
    &:nth-of-type(2) {
      top: 11px;
    }
    
    &:nth-of-type(3) {
      top: 22px;
    }
  }
}

.overlay-hamburger {
  position: fixed;
  background: $color-main;
  top: 0;
  left: 0;
  width: 100%;
  height: 0%;
  opacity: 0;
  visibility: hidden;
  transition: opacity .35s, visibility .35s, height .35s;
  overflow: hidden;
  
  &.open {
    opacity: 1;
    visibility: visible;
    height: 100%;
    
    li {
      animation: fadeInRight .5s ease forwards;
      animation-delay: .35s;
      
      &:nth-of-type(2) {
        animation-delay: .4s;
      }
      &:nth-of-type(3) {
        animation-delay: .45s;
      }
      &:nth-of-type(4) {
        animation-delay: .50s;
      }
      &:nth-of-type(5) {
        animation-delay: .55s;
      }
      &:nth-of-type(6) {
        animation-delay: .60s;
      }
    }
  }
  nav {
    position: relative;
    height: 70%;
    top: 50%;
    right: 5%;
    transform: translateY(-50%);
    font-size: 5vh;
    font-weight: 700;
  }
  ul {
    list-style: none;
    float: right;
    padding: 0;
    text-align: right;
    display: inline-block;
    position: relative;
    height: 100%; 
    
    li {
      display: block;
      position: relative;
      opacity: 0;
      margin: 10px 0;
      
      a {
        display: block;
        position: relative;
        color: $color-link;
        text-decoration: none;
        // overflow: hidden;
        &:hover {
          color: #f1a836!important;
        }
        &:before {
          counter-increment: navigation;
          content: "0" counter(navigation);
          position: absolute;
          transform: rotate(-90deg) translateX(-80px);
          font-size: 12px;
          font-weight: 300;
          color: #fff;
          margin-top: -65px;
          margin-left: -30px;
          transition: all 0.3s ease-in-out!important;
        }
        &:hover:before {
          margin-left: -50px;
        }
      }
    }
  }
}

@keyframes fadeInRight {
  0% {
    opacity: 0;
    left: 20%;
  }
  100% {
    opacity: 1;
    left: 0;
  }
}
